export class App {
  configureRouter(config, router) {
    this.router = router;
    config.title = 'UCCSS';
    config.map([
      { route: ['', 'home'], name: 'home', moduleId: PLATFORM.moduleName('./modules/home/home') },
      { route: 'user', name: 'User', moduleId: PLATFORM.moduleName('./modules/user/user'), settings: { auth: true, roles: [] } },
      { route: 'register', name: 'Register', moduleId: PLATFORM.moduleName('./modules/home/register'), settings: { auth: false, roles: [] } },
      { route: 'about', name: 'About', moduleId: PLATFORM.moduleName('./modules/home/about'), title: 'About the UCC', settings: { auth: false, roles: [] } },       
      { route: 'system', name: 'system', moduleId: PLATFORM.moduleName('./modules/admin/system/system'), settings: { auth: true, roles: [] } },
      { route: 'customers', name: 'customers', moduleId: PLATFORM.moduleName('./modules/admin/customers/customers'), settings: { auth: true, roles: [] } },
      { route: 'inventory', name: 'inventory', moduleId: PLATFORM.moduleName('./modules/admin/inventory/editInventory'), settings: { auth: true, roles: [] } },
      { route: 'site', name: 'site', moduleId: PLATFORM.moduleName('./modules/admin/site/site'), settings: { auth: true, roles: [] } },
      { route: 'documents', name: 'documents', moduleId: PLATFORM.moduleName('./modules/admin/documents/documents'), settings: { auth: true, roles: [] } },
      { route: 'support', name: 'support', moduleId: PLATFORM.moduleName('./modules/user/support/support'), settings: { auth: true, roles: [] } },
      { route: 'techHt/:HTNumber', name: 'techHt', moduleId: PLATFORM.moduleName('./modules/tech/support/support'), settings: { auth: true, roles: [] } },
      { route: 'clientRequests', name: 'clientRequests', moduleId: PLATFORM.moduleName('./modules/user/requests/clientRequests'), settings: { auth: true, roles: [] } },
      { route: 'profile', name: 'Profile', moduleId: PLATFORM.moduleName('./modules/user/profile'), settings: { auth: true, roles: [] } },
      { route: 'techRq', name: 'techRq', moduleId: PLATFORM.moduleName('./modules/tech/requests/techRequests'), settings: { auth: true, roles: [] } },
      { route: 'facco', name: 'facco', moduleId: PLATFORM.moduleName('./modules/facco/facco'), settings: { auth: true, roles: [] } },
      { route: 'analytics', name: 'analytics', moduleId: PLATFORM.moduleName('./modules/analytics/analytics'), settings: { auth: true, roles: [] } },
      { route: 'accprodrequests', name: 'accprodrequests', moduleId: PLATFORM.moduleName('./modules/acc/accRequests'), settings: { auth: true, roles: [] } },
      { route: 'accinstitutions', name: 'accinstitutions', moduleId: PLATFORM.moduleName('./modules/acc/accInstitutions'), settings: { auth: true, roles: [] } },
    ]);
  }
}

class AuthorizeStep {
  run(navigationInstruction, next) {
    if (navigationInstruction.getAllInstructions().some(i => i.config.settings.auth)) {
      var role = sessionStorage.getItem('role');
      if (!role) {
        return next.cancel(new Redirect('home'));
      }
    }
    return next();
  }
}
